*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.allday-wheather {
  background: #102136;
  box-shadow: 0 8px 43px rgba(16, 33, 54, .6);
}

.current-wheather-icon {
  color: #fff;
}

.current-wheather-city {
  color: #767d85;
  text-align: center;
  flex-direction: column;
  flex-shrink: 0;
  justify-content: center;
  font-family: Lato;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  display: flex;
}

.today-weather {
  flex-direction: column;
  flex-shrink: 0;
  justify-content: center;
  display: flex;
}

.today-weather__current {
  color: #fff;
  text-align: center;
  letter-spacing: 3.6px;
  font-family: Lato;
  font-size: 90px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}

.today-weather-minmax {
  display: flex;
}

.today-minmax__p {
  width: 75px;
  height: 20px;
  color: #767d85;
  text-align: center;
  flex-direction: column;
  flex-shrink: 0;
  justify-content: center;
  margin-bottom: 5px;
  font-family: Lato;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  display: flex;
}

.today-minmax svg {
  fill: #27384a;
  position: relative;
  bottom: 39px;
  left: 75px;
}

.today-minmax__mindegree, .today-minmax__maxdegree {
  width: 75px;
  height: 20px;
  color: #fff;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  font-family: Lato;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  display: flex;
}

.button-list {
  gap: 32px;
  display: flex;
  position: absolute;
}

.btn-today, .btn-5days {
  width: 182px;
  height: 27px;
  cursor: pointer;
  background: #fff;
  border-radius: 20px;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 0 12px;
  display: flex;
}

.btn-5days {
  background: rgba(255, 255, 255, .5);
}

.btn-5days:hover, .btn-5days:focus {
  background: #fff;
}

