}

.five-days__info {
  width: 100%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 15px;
  padding: 32px 32px 0;
  list-style: none;
  display: flex;
}

.five-days__item {
  flex-direction: column;
  align-items: center;
  gap: 5px;
  display: flex;
}

.five-days__day {
  color: rgba(255, 255, 255, .54);
  text-transform: capitalize;
  font-size: 12px;
  line-height: 1.2;
}

.five-days__date {
  text-transform: capitalize;
  font-size: 16px;
  line-height: 1.2;
}

.five-days__temperature {
  flex-direction: row;
  gap: 10px;
  padding-top: 20px;
  display: flex;
}

.five-days__button {
  color: rgba(255, 255, 255, .3);
  text-align: center;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 12px;
  font-weight: 700;
  line-height: 1.2;
  text-decoration: underline;
  display: block;
}

.temperature-min, .temperature-max {
  flex-direction: column;
  display: flex;
}

.temperature-min__title, .temperature-max__title {
  color: rgba(255, 255, 255, .54);
  margin-bottom: 10px;
  font-size: 12px;
}

.temperature-min__value, .temperature-max__value {
  color: #ff6b09;
  font-size: 14px;
}

.show-more {
  flex-direction: row;
  justify-content: space-between;
  gap: 10px;
  margin-top: 10px;
  display: flex;
}

.show-more__right {
  opacity: .5;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  display: block;
}

.show-more__left {
  opacity: .5;
  rotate: -180°;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

.show-more__icon {
  transform-origin: 50%;
  transform: scale(-1, 1);
}

.show-on-click {
  display: block;
}

.hide-on-click {
  display: none;
}

.more-info {
  width: 280px;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding-bottom: 20px;
  display: flex;
  overflow-x: auto;
}

.more-info__list {
  flex-direction: row;
  gap: 10px;
  list-style: none;
  display: flex;
}

.more-info__list :hover {
  background-color: rgba(255, 255, 255, .1);
}

.more-info__item {
  border: 1px solid rgba(255, 255, 255, .2);
  border-radius: 25px;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  padding: 20px 30px;
  list-style: none;
  display: flex;
}

.more-info__title {
  color: #fff;
  opacity: .54;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.2;
}

.more-info__temp {
  color: #ff6b09;
  flex-direction: column;
  gap: 5px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.2;
  display: flex;
}

.more-info__data {
  color: #fff;
  opacity: .54;
  flex-direction: column;
  gap: 5px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.2;
  list-style: none;
  display: flex;
}

.more-info__icon {
  width: 20px;
  height: 20px;
  display: block;
}

.data__pressure, .data__humidity, .data__wint {
  flex-direction: row;
  display: flex;
}

.show-chart {
  color: #ff6b09;
  cursor: pointer;
  background: none;
  border: none;
  flex-direction: row;
  align-items: center;
  gap: 5px;
  margin-top: 30px;
  margin-bottom: 36px;
  margin-left: 106px;
