html {
  box-sizing: border-box;
}

body {
  font-family: Lato;
}

*, :before, :after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

.container {
  width: 320px;
  margin: 0 auto;
  position: relative;
  overflow-x: hidden;
}

.container__background {
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}

.quote {
  width: 280px;
  color: #fff;
  text-align: -webkit-right;
  flex-direction: column;
  align-items: flex-end;
  padding-left: 20px;
  padding-right: 20px;
  font-weight: 400;
  display: flex;
}

.quote__text {
  margin-bottom: 20px;
  font-size: 14px;
  font-style: italic;
  line-height: 1.2;
}

.quote__author {
  font-size: 18px;
  line-height: 1.2;
}

.quote__icon {
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
  width: 20px;
  height: 20px;
  display: inline-block;
}

@media screen and (min-device-pixel-ratio: 2), screen and (min-resolution: 192dpi), screen and (min-resolution: 2x) {
  .container__background {
    width: 100%;
    background-color: rgba(0, 0, 0, 0);
    background-image: linear-gradient(.56deg, #000 -13.48%, rgba(0, 0, 0, 0) 78.75%), url("background1-t@x2.17ccabab.jpg");
    background-position: 0 0, 0 0;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: scroll, scroll;
    background-origin: padding-box, padding-box;
    background-clip: border-box, border-box;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    overflow-y: hidden;
  }

  .container__background {
    width: 100%;
    background-color: rgba(0, 0, 0, 0);
    background-image: linear-gradient(.56deg, #000 -13.48%, rgba(0, 0, 0, 0) 78.75%), url("background1-t.a5752d10.jpg");
    background-position: 0 0, 0 0;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: scroll, scroll;
    background-origin: padding-box, padding-box;
    background-clip: border-box, border-box;
